<template>
  <div class="movie-detail">
    <div class="movie-detail-item" v-if="LOADING">
      <vcl-facebook></vcl-facebook>
    </div>
    <div class="movie-detail-item" v-if="!LOADING">
      <div class="image">
        <img :src="movie.poster_path | setImagePath" />
      </div>
      <div class="content">
        <div class="head">
          <div>{{movie.original_title}}</div>
          <div>{{movie.tagline}}</div>
        </div>
        <p class="title">THE INFO</p>
        <div
          class="small-info"
        >{{ movie.spoken_languages.length > 0 ? movie.spoken_languages[0].name + ' /' : ''}} {{movie.runtime}} MIN / {{movie.release_date}}</div>
        <p class="title">THE GENRES</p>
        <div class="genres-info">
          <p v-for="item in movie.genres" :key="item.id">
            <router-link :to="{ name: 'genres', params: { genres: item.name }}" :key="item.id">
              <span></span>
              {{item.name}}
            </router-link>
          </p>
        </div>
        <p class="title">THE description</p>
        <div class="desc">
          <p>{{movie.overview}}</p>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import { mapState, mapGetters } from "vuex";
import { VclFacebook } from "vue-content-loading";

export default {
  head: {
    title: {
      inner: "Movie Detail"
    }
  },
  computed: {
    ...mapState({
      movie: state => state.movie.singleMovie
    }),
    ...mapGetters({
      LOADING: "movie/getMovieLoader"
    })
  },
  mounted() {
    this.initMovieDetail();
  },
  methods: {
    initMovieDetail() {
      let id = this.$route.params.id;
      this.$store.dispatch("movie/getDetail", { id });
    }
  },
  watch: {
    $route() {
      this.initMovieDetail();
    }
  },
  components: { VclFacebook }
};
</script>


